@mixin container($pr: 16px, $pl: 16px) {
  width: 100%;
  padding-right: $pr;
  padding-left: $pl;
  margin-right: auto;
  margin-left: auto;

  @media (min-width: 576px) {
    max-width: 540px;
  }

  @media (min-width: 768px) {
    max-width: 720px;
  }

  @media (min-width: 992px) {
    max-width: 960px;
  }

  @media (min-width: 1200px) {
    max-width: 1140px;
  }

  @media (min-width: 1400px) {
    //max-width: 1320px;
    //max-width: 1380px;
    max-width: 1420px;
  }
}
